<template>
  <div>
    <h3>{{step.title}}</h3>
    <p>{{step.info}}</p>
    <div v-bind:key="stepc.id" v-for="stepc in step.children">
      <div class="ml-4">
        <input type="radio" :name="'choice'+step.id" v-model="step.showChild" :value="stepc">
        {{stepc.title}}
        <br>
        {{stepc.explanation}}
        <br>
        <div v-if="preview == true" class="ml-4">
          <div v-if="stepc.children">
            (Die folgenden Optionen werden verfügbar sein:)
            <br>
          </div>
          <div v-for="stepcc in stepc.children" v-bind:key="stepcc.id" class="ml-2">
            - {{stepcc.title}}
            <br>
          </div>
          <br>
        </div>
      </div>
      <hr>
      </div>
      <div v-if="step.showChild">
        <StepView v-bind:step="step.showChild" v-bind:preview="preview"/>
      </div>
  </div>
</template>

<script>
export default {
  name: "StepView",
  props: ["step", "preview"]
};
</script>

